.input {
    text-align: center;
    margin-top: 5rem;
}

input {
    display: none;
}

label {
    width: 100%;
    padding: 3% 10%;
    cursor: pointer;
    border: 1px solid #00092C;
    border-radius: 5%;
    background-color: #FF5F00;
    font-size: 100%;
    font-weight: 800;
    color: #fff;
}

label:hover{
    background-color: #00092C;
    color: #fff;
}